.ditty-page {
  background: #fff;
  overflow: hidden;
}
.ditty-page #wpcontent {
  display: flex;
  flex-direction: row;
  padding-left: 0;
}
.ditty-page #wpbody {
  flex: 1;
}
.ditty-page #wpbody-content {
  padding: 0;
  float: none;
}

.ditty-adminPage {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  height: 100vh;
  overflow: hidden;
  box-sizing: border-box;
}
.ditty-adminPage * {
  box-sizing: border-box;
}
.admin-bar .ditty-adminPage {
  height: calc(100vh - 32px);
}
.ditty-adminPage__app {
  flex: 1;
  display: flex;
  flex-wrap: nowrap;
  height: calc(100% - 100px);
}
.ditty-adminPage__app__sidebar {
  position: relative;
  width: 350px;
  height: 100%;
  background: #fff;
  border-right: 1px solid #ccc;
  box-sizing: border-box;
}
.ditty-adminPage__app__content {
  flex: 1;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
.ditty-adminPage__app__sizer {
  position: absolute;
  top: 0;
  left: -5px;
  z-index: 9;
  width: 5px;
  height: 100%;
  background: rgba(170, 170, 170, 0.2);
  cursor: ew-resize;
  transition: background-color 0.25s ease;
}
.ditty-adminPage__app__sizer:hover {
  background-color: #aaa;
}
.ditty-adminPage .Toastify__toast {
  background-color: #fff;
}
.ditty-adminPage .Toastify__toast .ditty-logo {
  fill: #19bf7c;
}
.ditty-adminPage .Toastify__progress-bar {
  background: #19bf7c;
}

.admin-bar .ditty-adminPage .Toastify__toast-container--top-right {
  top: calc(1em + 32px);
}

@media screen and (max-width: 782px) {
  .admin-bar .ditty-adminPage {
    height: calc(100vh - 46px);
  }
  .admin-bar .ditty-adminPage .Toastify__toast-container--top-right {
    top: calc(1em + 46px);
  }
}
@media screen and (min-width: 782px) {
  .admin-bar .ditty-adminPage {
    height: calc(100vh - 46px);
  }
}
.ditty-button {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 5px;
  font-size: 14px;
  line-height: 20px;
  font-weight: 500;
  text-align: center;
  text-decoration: none;
  color: #23282d;
  background: rgba(0, 0, 0, 0.2);
  border: none;
  border-radius: 3px;
  padding: 10px 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
  transition: color 0.25s ease, background-color 0.25s ease;
}
.ditty-button.is-primary {
  color: #fff;
  background: #19bf7c;
}
.ditty-button.is-small {
  font-size: 13px;
  padding: 5px 10px;
}
.ditty-button:disabled {
  cursor: default;
  opacity: 0.4;
}

.ditty-button-group {
  display: flex;
  gap: 10px;
}
.ditty-button-group.is-tabbed {
  align-items: stretch;
  justify-content: flex-start;
  background: #32373c;
  border-radius: 3px;
  overflow: hidden;
  gap: 1px;
}
.ditty-button-group.is-tabbed .ditty-button {
  border-radius: 0;
}

.ditty-filter {
  display: flex;
  align-items: flex-end;
  gap: 5px;
}
.ditty-filter__search {
  flex: 1;
}
.ditty-filter .ditty-button {
  height: 36px;
}
.ditty-filter .ditty-button.active {
  color: #fff;
  background: #19bf7c;
}

.ditty-icon-block {
  display: flex;
}
.ditty-icon-block__icon {
  flex: 0 0 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: #ccc;
  border-radius: 3px;
  margin-right: 10px;
}
.ditty-icon-block__icon svg {
  width: 40%;
  height: 40%;
  min-width: 15px;
  min-height: 15px;
  max-width: 25px;
  max-height: 25px;
}
.ditty-icon-block__contents {
  flex: 1;
}
.ditty-icon-block__contents h1,
.ditty-icon-block__contents h2,
.ditty-icon-block__contents h3,
.ditty-icon-block__contents h4,
.ditty-icon-block__contents h5,
.ditty-icon-block__contents h6,
.ditty-icon-block__contents p {
  margin: 0 0 5px;
}
.ditty-icon-block__contents > *:last-child {
  margin-bottom: 0;
}

.ditty-icon-button {
  height: 60px;
  min-width: 60px;
  flex: 0 0 auto;
  flex-direction: column;
  align-items: center;
  font-size: 14px;
  color: rgba(29, 35, 39, 0.8);
  background: rgba(0, 0, 0, 0.02);
  border: none;
  border-radius: 3px;
  padding: 5px;
}
.ditty-icon-button:hover:not(.ditty-icon-button--active) {
  color: #1d2327;
  background: rgba(0, 0, 0, 0.05);
}
.ditty-icon-button--active {
  background: #19bf7c;
}
.ditty-icon-button--active .ditty-icon-button__icon {
  display: block;
  color: #fff;
}
.ditty-icon-button--active .ditty-icon-button__label {
  color: #fff;
}
.ditty-icon-button__icon {
  height: 20px;
  flex: 0 0 auto;
}
.ditty-icon-button__label {
  display: block;
  font-size: 11px;
  font-weight: 400;
  margin-top: 5px;
  flex: 0 0 auto;
}

.ditty-link {
  color: #323438;
  text-decoration: underline;
  border: none;
  background: transparent;
  padding: 0;
  cursor: pointer;
}
.ditty-link:hover {
  color: #1d2327;
}

