}

#ditty-editor__adminbar {
  flex: 0 0 auto;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  padding: 20px;
  background: #fff;
  border-bottom: 1px solid #ccc;
  box-sizing: border-box;
}
#ditty-editor__adminbar * {
  box-sizing: border-box;
}
#ditty-editor__adminbar h2 {
  margin: 0;
}
#ditty-editor__adminbar__title {
  flex: 1;
}
#ditty-editor__adminbar button.ditty-has-updates {
  color: #fff;
  background: #19bf7c;
}

#ditty-editor__footerbar {
  flex: 0 0 auto;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #fff;
  border-top: 1px solid #ccc;
  box-sizing: border-box;
}
#ditty-editor__footerbar * {
  box-sizing: border-box;
}
#ditty-editor__footerbar h2 {
  margin: 0;
}

.ditty-tabs {
  flex: 0 0 auto;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: flex-start;
  height: 40px;
  font-size: 14px;
  background: #e8e9eb;
}

.ditty-tab {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  padding: 0 15px;
  text-align: center;
  text-decoration: none;
  color: #616970;
  background: #f2f3f5;
  border-radius: 0;
  border: none;
  cursor: pointer;
  transition: color 0.25s ease, background-color 0.25s ease;
}
.ditty-tab:hover:not(.ditty-tab--active) {
  color: #fff;
  background-color: #19bf7c;
}
.ditty-tab--active {
  color: #19bf7c;
  background-color: #fff;
}
.ditty-tab__icon {
  flex: 0 0 auto;
  display: block;
  font-size: 18px;
}
.ditty-tab__label {
  flex: 1;
}

.ditty-tabs--primary {
  height: 60px;
  background: #1d2327;
}
.ditty-tabs--primary .ditty-tab {
  width: 62px;
  color: rgba(255, 255, 255, 0.8);
  background: #1d2327;
}
.ditty-tabs--primary .ditty-tab__label {
  display: none;
}
.ditty-tabs--primary .ditty-tab:hover:not(.ditty-tab--active) {
  color: #fff;
  background-color: rgba(255, 255, 255, 0.2);
}
.ditty-tabs--primary .ditty-tab--active {
  width: auto;
  color: #fff;
  background-color: #19bf7c;
  padding-left: 30px;
  padding-right: 30px;
}
.ditty-tabs--primary .ditty-tab--active .ditty-tab__label {
  display: block;
}
.ditty-tabs--primary .ditty-tab--active .ditty-tab__icon {
  display: none;
}

.ditty-tabs--secondary {
  background: #f2f3f5;
  border-bottom: 1px solid #ccc;
}

.ditty-tabs--cloud {
  gap: 5px;
  flex-wrap: wrap;
  height: auto;
  background: #fff;
  color: #1d2327;
}
.ditty-tabs--cloud .ditty-tab {
  width: 70px;
  height: 60px;
  flex: 1 0 auto;
  flex-direction: column;
  align-items: center;
  color: rgba(29, 35, 39, 0.8);
  background: rgba(0, 0, 0, 0.02);
  border-radius: 3px;
  padding: 5px;
}
.ditty-tabs--cloud .ditty-tab:hover:not(.ditty-tab--active) {
  color: #1d2327;
  background: rgba(0, 0, 0, 0.05);
}
.ditty-tabs--cloud .ditty-tab--active {
  background: #19bf7c;
}
.ditty-tabs--cloud .ditty-tab--active .ditty-tab__icon {
  display: block;
  color: #fff;
}
.ditty-tabs--cloud .ditty-tab--active .ditty-tab__label {
  color: #fff;
}
.ditty-tabs--cloud .ditty-tab__icon {
  height: 20px;
  flex: 0 0 auto;
}
.ditty-tabs--cloud .ditty-tab__label {
  display: block;
  font-size: 11px;
  font-weight: 400;
  margin-top: 5px;
  flex: 0 0 auto;
}

.ditty-tabs--list {
