.ditty-field--panel-heading {
  padding-top: 20px;
  padding-bottom: 20px;
}
.ditty-field--panel-heading > .ditty-field__heading > .ditty-field__heading__contents > .ditty-field__label {
  font-size: 16px;
}

.ditty-field__input--slider .components-range-control__track {
  color: #19bf7c;
}
.ditty-field__input--slider .components-range-control__track + span span {
  background-color: #19bf7c;
}
.ditty-field__input--slider .components-range-control__track + span span:before {
  background-color: rgba(25, 191, 124, 0.5);
}

.ditty-field__input--spacing,
.ditty-field__input--radius {
  display: flex;
  gap: 5px;
}
.ditty-field__input--spacing__box,
.ditty-field__input--radius__box {
  flex: 0 0 auto;
  position: relative;
  width: 20px;
  height: 20px;
}
.ditty-field__input--spacing__box span,
.ditty-field__input--radius__box span {
  position: absolute;
  display: block;
  background: #ccc;
}
.ditty-field__input--spacing__box span.active,
.ditty-field__input--radius__box span.active {
  background-color: #1d2327;
}
.ditty-field__input--spacing__inputs,
.ditty-field__input--radius__inputs {
  display: flex;
  gap: 5px;
  flex-wrap: wrap;
}
.ditty-field__input--spacing__input,
.ditty-field__input--radius__input {
  width: calc(50% - 5px);
}
.ditty-field__input--spacing .ditty-field--unit,
.ditty-field__input--radius .ditty-field--unit {
  padding: 0;
  border: none;
}

.ditty-field__input--spacing__box span:nth-child(1) {
  top: 0;
  left: 3px;
  width: calc(100% - 6px);
  height: 2px;
}
.ditty-field__input--spacing__box span:nth-child(2) {
  bottom: 0;
  left: 3px;
  width: calc(100% - 6px);
  height: 2px;
}
.ditty-field__input--spacing__box span:nth-child(3) {
  top: 3px;
  left: 0;
  height: calc(100% - 6px);
  width: 2px;
}
.ditty-field__input--spacing__box span:nth-child(4) {
  top: 3px;
  right: 0;
  height: calc(100% - 6px);
  width: 2px;
}

.ditty-field__input--radius__box {
  border-radius: 5px;
  overflow: hidden;
}
.ditty-field__input--radius__box:before {
  position: absolute;
  top: 2px;
  left: 2px;
  content: "";
  width: 16px;
  height: 16px;
  background: #fff;
  z-index: 9;
  transform: rotate(45deg);
}
.ditty-field__input--radius__box:after {
  position: absolute;
  top: 2px;
  left: 2px;
  content: "";
  width: 16px;
  height: 16px;
  background: #fff;
  border-radius: 3px;
  z-index: 9;
}
.ditty-field__input--radius__box span {
  width: 10px;
  height: 10px;
}
.ditty-field__input--radius__box span:nth-child(1) {
  top: 0;
  left: 0;
}
.ditty-field__input--radius__box span:nth-child(2) {
  top: 0;
  right: 0;
}
.ditty-field__input--radius__box span:nth-child(3) {
  bottom: 0;
  left: 0;
}
.ditty-field__input--radius__box span:nth-child(4) {
  bottom: 0;
  right: 0;
}

.ditty-field__input--radio div[role=radiogroup] {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.ditty-field__input--radio.ditty-field__input--inline div[role=radiogroup] {
  flex-direction: row;
  gap: 15px;
}

.ditty-field__input--unit {
  display: flex;
  align-items: stretch;
}
.ditty-field__input--unit input {
  border-top-right-radius: 0 !important;
  border-bottom-right-radius: 0 !important;
  border-right: none !important;
}
.ditty-field__input--unit select {
  flex: 0 0 auto;
  width: 40px;
  font-size: 12px;
  text-align: center;
  padding-left: 5px;
  padding-right: 5px;
  background: transparent;
  border-top-left-radius: 0 !important;
  border-bottom-left-radius: 0 !important;
}

.ditty-field-list .ditty-field--layoutTagField {
  padding: 0;
  border-bottom: none;
}
.ditty-field--layoutTagField > .ditty-field__heading {
  display: flex;
  align-items: center;
  padding: 15px;
  background: #fbfbfb;
  border-bottom: 1px solid #dfdfdf;
}
.ditty-field--layoutTagField > .ditty-field__heading > .ditty-field__heading__contents > .ditty-field__label {
  margin-bottom: 0;
}
.ditty-field--layoutTagField > .ditty-field__heading > .ditty-field__heading__contents > .ditty-field__help {
  margin-bottom: 0;
}
.ditty-field--layoutTagField > .ditty-field__heading > .ditty-field__heading__contents > .ditty-field__description {
  margin-bottom: 0;
}
.ditty-field--layoutTagField .layoutTagActions {
  flex: 0 0 auto;
  margin-left: auto;
}
.ditty-field--layoutTagField .layoutTagActions span {
  flex: 0 0 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
  cursor: pointer;
  border-radius: 3px;
  background: rgba(0, 0, 0, 0.05);
  transition: background-color 0.25s ease;
}
.ditty-field--layoutTagField:not(.is-disabled) .layoutTagAction__enable {
  color: #fff;
  background: #19bf7c;
}
.ditty-field--layoutTagField.is-disabled .layoutTagAction__disable {
  color: #fff;
  background: #be1919;
}
.ditty-field--layoutTagField.is-customized .layoutTagAction__customize {
  color: #fff;
  background: #1976be;
}

#ditty-settings__navigation {
  background: #32373b;
}
#ditty-settings__navigation .ditty-tabs--list {
  background: transparent;
}
#ditty-settings__navigation .ditty-tabs--list .ditty-tab {
  color: #fff;
  background: transparent;
}
#ditty-settings__navigation .ditty-tabs--list .ditty-tab--active {
  background: #19bf7c;
}
#ditty-settings__navigation .ditty-tabs--list .ditty-tab:not(.ditty-tab--active):hover {
  color: #19bf7c;
  background-color: rgba(0, 0, 0, 0.5);
}

@media screen and (max-width: 782px) {
  #ditty-settings {
    flex-direction: row;
  }
  #ditty-settings__navigation {
    width: 44px;
    flex: 0 0 44px;
  }
  #ditty-settings__navigation .ditty-tabs--list .ditty-tab {
    padding: 10px 4px 10px 14px;
    width: auto;
  }
  #ditty-settings__navigation .ditty-tabs--list .ditty-tab__label {
    display: none;
  }
}
@media screen and (min-width: 782px) {
  #ditty-settings__navigation {
    width: 250px;
  }
}